import React from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import avatar from '../img/ice-bear-ninja.webp'
import { setBurger } from '../redux/reducers/mainReducer'
import AboutTabs from './AboutTabs'

const About = () => {
  const dispatch = useDispatch()
  const linkHandler = () => {
    dispatch(setBurger(false))
    document.querySelector('.burger').classList.add('hide')
    document.querySelector('.toggle').classList.add('hide')
  }
  setTimeout(() => {
    document.querySelector('section').classList.add('active')
    document.querySelector('.toggle').classList.remove('hide')
    document.querySelector('.burger').classList.remove('hide')
  }, 500)

  const skills = [
    'HTML',
    'CSS',
    'javascript',
    'react',
    'redux',
    'jquery',
    'Sass, less, stylus',
    'bootstrap',
    'tailwind',
    'BEM-methodology',
    ' node JS',
    ' mongo DB',
    'git',
    'ctrl + c ctrl + v',
  ]

  return (
    <section className="about padding" id="about">
      <div className="container">
        <div className="row">
          <div className="title">
            <h2>about me</h2>
          </div>
        </div>
        <div className="row">
          <div className="p-15 col-12 col-md-5">
            <div className="about__img-box border-40">
              <img src={avatar} alt="" />
            </div>
          </div>
          <div className="about__text p-15 col-12 col-md-7">
            <p>
              Lorem ipsum dolor, sit amet consectetur adipisicing elit. Aliquid
              deserunt nostrum facilis aspernatur ut quo nihil soluta hic optio
              ipsam voluptates laboriosam nulla illo sit, vero vel ex error
              recusandae vitae. Earum cumque non temporibus quaerat repellat
              quos amet itaque. Illum numquam iure laudantium officiis? Eum
              accusantium natus sunt atque!
            </p>
            <h3>skills</h3>
            <div className="skills">
              {skills.map((skill) => (
                <p className="skills-item">{skill}</p>
              ))}
            </div>
            <AboutTabs />
            <Link
              to="/portfolio-react/about"
              className="btn link-item"
              datahash="#about"
            >
              download cv
            </Link>
            <Link
              to="/portfolio-react/contact"
              className="btn link-item"
              datahash="#contact"
              onClick={linkHandler}
            >
              Contact me
            </Link>
          </div>
        </div>
      </div>
    </section>
  )
}

export default About
