import React from 'react'

import { Link } from 'react-router-dom'
const Portfolio = ({ portfolioData }) => {
  const portfolioHandler = () => {
    setTimeout(() => {
      document.querySelector('.burger').classList.add('hide')
      document.querySelector('.toggle').classList.add('hide')
      document.querySelector('.portfolio-popup').classList.add('open')
    }, 1)
  }
  setTimeout(() => {
    document.querySelector('section').classList.add('active')
    document.querySelector('.toggle').classList.remove('hide')
    document.querySelector('.burger').classList.remove('hide')
  }, 300)
  return (
    <section className="portfolio padding" id="portfolio">
      <div className="container">
        <div className="row">
          <div className="title">
            <h2>recent work</h2>
          </div>
        </div>
        <div className="row">
          {portfolioData.map((el) => {
            return (
              <div
                className="portfolio-item col-12 col-md-6 col-lg-4"
                key={el.id}
              >
                <Link to={`/portfolio-react/portfolio/${el.name}`}>
                  <img
                    className="portfolio-item-img"
                    src={el.img}
                    alt={el.title}
                    onClick={portfolioHandler}
                  />
                </Link>
                <h3 className="portfolio-item-title">{el.title}</h3>
                <a
                  href={el.link}
                  target="_blank"
                  className="btn"
                  rel="noreferrer"
                >
                  View online
                </a>
              </div>
            )
          })}
        </div>
      </div>
    </section>
  )
}

export default Portfolio
