import React, { useState } from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import About from './components/About'
import BgCircles from './components/BgCircles'
import Contact from './components/Contact'
import Header from './components/Header'
import Home from './components/Home'
import Overlay from './components/Overlay'
import PageLoader from './components/PageLoader'
import Portfolio from './components/Portfolio'
import initPortfolioData from './components/portfolioData'
import PortfolioPopup from './components/PortfolioPopup'
import './style/style.min.css'
window.addEventListener('load', () => {
  document.querySelector('.main').classList.remove('hidden')
  if (document.querySelector('section')) {
    document.querySelector('section').classList.add('active')
  }
  document.querySelector('.page-loader').classList.add('fade-out')
  setTimeout(() => {
    document.querySelector('.page-loader').style.display = 'none'
  }, 600)
})

const App = () => {
  const [portfolioData, setPortfolioData] = useState(initPortfolioData)
  return (
    <BrowserRouter>
      <div className="main">
        <Header />
        <Switch>
          <Route exact path="/portfolio-react">
            <Home />
          </Route>
          <Route exact path="/portfolio-react/about">
            <About />
          </Route>
          <Route exact path="/portfolio-react/portfolio">
            <Portfolio portfolioData={portfolioData} />
          </Route>
          <Route exact path="/portfolio-react/portfolio/:projectName">
            <PortfolioPopup portfolioData={portfolioData} />
          </Route>
          <Route exact path="/portfolio-react/contact">
            <Contact />
          </Route>
        </Switch>
      </div>
      <PageLoader />
      <BgCircles />
      <Overlay />
    </BrowserRouter>
  )
}

export default App
