import React from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import avatar from '../img/ice-bear-seal.webp'
import { setBurger } from '../redux/reducers/mainReducer'
import Parallax from './Parallax'
const Home = () => {
  Parallax()
  const dispatch = useDispatch()
  const linkHandler = () => {
    dispatch(setBurger(false))
    document.querySelector('.burger').classList.add('hide')
    document.querySelector('.toggle').classList.add('hide')
  }
  setTimeout(() => {
    document.querySelector('section').classList.add('active')
    document.querySelector('.toggle').classList.remove('hide')
    document.querySelector('.burger').classList.remove('hide')
  }, 500)
  return (
    <section className="home align-items-center" id="home">
      <div className="container">
        <div className="row align-items-center">
          <div className="home__text p-15 col-12 col-lg-6">
            <p>hello, i'm</p>
            <h1>amantay suranaliev</h1>
            <h2>frontend web - developer</h2>
            <Link
              to="/portfolio-react/about"
              className="btn home__btn link-item"
              datahash="#about"
              onClick={linkHandler}
            >
              more about me
            </Link>
            <Link
              to="/portfolio-react/portfolio"
              className="btn home__btn link-item"
              datahash="#portfolio"
              onClick={linkHandler}
            >
              portfolio
            </Link>
          </div>
          <div className="p-15 col-12 col-lg-6">
            <div className="home__img-box">
              <img className="home__avatar" src={avatar} alt="profile avatar" />
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Home
